export default {
  name: 'actItem',
  props:['value'],
  data () {
    return {
      ID: this.value.ID,
      IMG: this.value.IMG,
      NAME: this.value.NAME,
      TYPE: this.value.TYPE,
    }
  },
  methods: {
    goToMoreFn() {
      this.$router.push({path: "/mainpage/home/allActivity/ActDetail/" + this.ID});            
    }
  }
}



