import ActItem from "./ActItem";
import HeaderBack from '../Common/HeaderBack'
export default {
  name: 'allActivity',
  components: {
    myHeader: HeaderBack,
    myActItem: ActItem 
  },
  data () {
    return {
      header: {
        HBL_icon:"icon_back", 
        HBC_text:"乐土推荐活动系列", 
        HBR_icon: " ",
      },
      // 热卖商品数据
      allActivitys:[],
      activitys: [
        {
          ID: 'act001',  
          NAME: 'Number One',
          TYPE: 'One',
          IMG: require('../../assets/images/home/activity_02.jpg'),
        },
        {
          ID: 'act002',  
          NAME: 'Number Two',
          TYPE: 'Two',
          IMG: require('../../assets/images/home/activity_03.jpg'),
        },
        {
          ID: 'act003',  
          NAME: 'Number Three',
          TYPE: 'Three',
          IMG: require('../../assets/images/home/activity_02.jpg'),
        },
        {
          ID: 'act004',  
          NAME: 'Number Four',
          TYPE: 'Four',
          IMG: require('../../assets/images/home/activity_03.jpg'),
        },
      ],
    }
  },
  methods: {
    goToAll () {
      this.$router.push({path: '/mainpage/all'});
    }
  },
  created () {
    // 数据加载 
    comTools.showLoading(this);
    setTimeout((data) => {
      this.allActivitys = this.activitys;
      // 数据加载 结束
      comTools.hideLoading(this);
    }, 500)
  }
}



